.search-form {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.search-input {
  min-width: 400px;
  height: 50px;
  padding-left: 20px;

  color: rgb(71, 197, 255);
  outline: none;
  border-radius: 5px;
  border-color: rgb(71, 197, 255);

  &::placeholder {
    color: rgb(71, 197, 255);
  }
}

.more-img-btn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 16px;

  color: #fff;
  background-color: #2196f3;
  border: 0;
  outline: 0;
  padding: 8px 24px;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.05);
  font: inherit;
  font-weight: 500;

  &:hover {
    box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.15);
    background-color: #1976d2;
  }
}
